import { Component, Host, h, Prop } from '@stencil/core';

@Component({
	tag: 'o-avatar',
	styleUrl: 'o-avatar.scss',
	shadow: true
})
export class OAvatar {
	/** Optional full name (first and last) */
	@Prop() initials?: string;

	/** Label for Accessability purposes  */
	@Prop() label: string;

	/** Ensure only up to 2 alphabetic characters are used */
	private get characters(): string | null {
		if (!this.initials) return null;
		return this.initials
			.replace(/[^a-zA-Z]/g, '')
			.toUpperCase()
			.slice(0, 2);
	}

	render() {
		return (
			<Host>
				<div
					class="size-12 flex grow-0 shrink-0 items-center justify-center rounded-radius-full border-width-sm border-border-brand bg-background-brand text-content-brand body-default"
					aria-label={this.label}
				>
					{this.characters ?? <o-icon name="user" color="oec_blue" size="md"></o-icon>}
				</div>
			</Host>
		);
	}
}
